import styled from "styled-components";
import React from 'react';
import { SearchCounryResp } from '../models/models';
import { useSortCountriesQuery } from "../store/countries/countries.api";
import { Link } from "react-router-dom";

const Wrapper = styled.section`
	margin-top: 3rem;
	width: 100%;
	display: grid;
	grid-template-columns: 100%;
	gap: 2rem;

	@media(min-width: 767px) {
		grid-template-columns: minmax(100px, 400px) 1fr;
		align-items: center;
		gap: 5rem;
	}
	@media(min-width: 1024px) {
		grid-template-columns: minmax(400px, 600px) 1fr;
	}
`;

const InfoImage = styled.img`
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
`;

const InfoTitle = styled.h1`
	margin: 0;
	font-weight: var(--fw-normal);
`;

const ListGroup = styled.div`
	display: flex;
	flex-direction: column;
	gap: 2rem;

	@media (min-width: 1024px) {
		flex-direction: row;
		gap: 4rem;
	}
`;

const List = styled.ul`
	list-style: none;
	margin: 0;
	padding: 0;
`;

const ListItem = styled.li`
	line-height: 1.8;
	& > b {
		font-weight: var(--fw-bold);
	}
`;

const Meta = styled.div`
	margin-top: 3rem;
	display: flex;
	gap: 1.5rem;
	flex-direction: column;
	align-items: flex-start;

	& > b {
		font-weight: var(--fw-bold);
	}

	@media (min-width: 767px) {
		flex-direction: row;
		align-items: center;
	}
`;

const TagGroup = styled.div`
	display: flex;
	gap: 1rem;
	flex-wrap: wrap;
`;

const Tag = styled(Link)`
	text-decoration: none;
	color: var(--colors-text);
	padding: 0 1rem;
	background-color: var(--colors-ui-base);
	box-shadow: var(--shadow);
	line-height: 1.5;
	cursor: pointer;
`;

const Info: React.FC<SearchCounryResp> = (props) => {
	const {
		name,
		nativeName,
		flag,
		capital,
		population,
		region,
		subregion,
		topLevelDomain = [],
		currencies = [],
		languages = [],
		borders = [],
	} = props;

	const { data } = useSortCountriesQuery(borders);
	const dataNames = data?.map(c => c.name);

	return (
		<Wrapper>
			<InfoImage src={flag} alt={name} />
			<div>
				<InfoTitle>{name}</InfoTitle>
				<ListGroup>
					<List>
						<ListItem>
							<b>Native Name:</b> {nativeName}
						</ListItem>
						<ListItem>
							<b>Population:</b> {population}
						</ListItem>
						<ListItem>
							<b>Region:</b> {region}
						</ListItem>
						<ListItem>
							<b>Sub Region:</b> {subregion}
						</ListItem>
						<ListItem>
							<b>Capital:</b> {capital}
						</ListItem>
					</List>
					<List>
						<ListItem>
							<b>Top Level Domain</b>{' '}
							{topLevelDomain.map((d) => (
								<span key={d}>{d}</span>
							))}
						</ListItem>
						<ListItem>
							<b>Currency</b>{' '}
							{currencies.map((c) => (
								<span key={c.code}>{c.name}</span>
							))}
						</ListItem>
						<ListItem>
							<b>Language</b>{' '}
							{languages.map((l) => (
								<span key={l.name}>{l.name}</span>
							))}
						</ListItem>
					</List>
				</ListGroup>
				<Meta>
					<b>Border Countries</b>
					{!dataNames?.length ? (
						<span>There is no borders countries</span>
					) : (
						<TagGroup>
							{dataNames.map((b) => {
								return <Tag to={`/country/:${b}`} key={b}>{b}</Tag>
							})}
						</TagGroup>
					)}
				</Meta>
			</div>
		</Wrapper>
	);
};

export default Info;